.btn-linkedin {
    background: rgb(194, 63, 107);
    border-radius: 0;
    color: #fff;
    border-width: 0px;
    border-style: solid;
    border-color: #084461;
    width: auto;
    border-radius: 10px;
    margin-left: 2px;
    margin-right: 2px;
}
.btn-linkedin:link,
.btn-linkedin:visited {
    color: #fff;
}
.btn-linkedin:active,
.btn-linkedin:hover {
    background: #084461;
    color: #fff;
}

.vertical_dotted_line {
    border-left: 1px dotted black;
    height: 300px;
    align-items: center;
}

.spaceForIcon {
    margin-top: 22%;
}
