div.indentedBox {
    height: 50vh;
    width: auto;
    background: #fdfdfd; /* kolor tła boxu - najlepiej ustawić ciemniejszy od tła (lepszy efekt) */
    box-shadow: inset 0px 0px 10px 0px rgb(143, 143, 143); /* cień do środka */
    border-bottom: 1px solid #c5c5c5; /* obramowanie dolne */
    /* border-right: 1px solid #4b4b4b; obramowanie prawe */
    border-radius: 25px;
    padding-top: 15px;
    padding-right: 10px;
    text-align: left;
}
