.roundImageShadow {
    max-width: 100%;
    height: auto;
    border-radius: 25px;
    padding: 4px;
    border: 2px solid #021a40;
    background: radial-gradient(circle, transparent 50%, black 150%);
    background-color: rgb(204, 204, 204);
}

.firstClass {
    transform: translate(0%, 0%);
}

.roundImageShadowInterests {
    position: relative;
    display: block;
    text-align: center;
    line-height: 60px;
    transition: 0.5s;
}

.roundImageShadowInterests:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 25px;
    background: #ffffff;
    transition: 0.5s;
    transform: scale(0.8);
    z-index: -1;
}

.roundImageShadowInterests:hover:before {
    transform: scale(1);
    box-shadow: 0px 0px 10px #ffffff;
}

.centreImages {
